@import "utils";

.header {
  height: 80px;
  width: 100%;

  position: sticky;
  top: 0;

  z-index: 1000;

  background-color: white;

  &__container {
    height: 100%;
    display: flex;
    align-items: center;
    &-left {
      display: flex;
      align-items: center;
      flex: 2;
    }
    &-right {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      flex: 1;
    }
  }
  &__location {
    @include button(60px, "https://nikitaushenko.github.io/react_uber-eats/images/Checkin.svg");
    &-form {
      position: relative;
      width: 350px;
      flex: 1;
    }
    input {
      box-sizing: border-box;

      position: absolute;
      left: 0;

      height: 100%;
      width: 100%;

      padding: 0 0 0 46px;

      font-size: 16px;
      color: $text-color;
    }
  }
  &__search {
    @include button(0, "https://nikitaushenko.github.io/react_uber-eats/images/Loop.svg");
    &-form {
      position: relative;
      width: 350px;
      flex: 1;
    }
    input {
      box-sizing: border-box;

      position: absolute;
      left: 0;

      height: 100%;
      width: 100%;

      padding: 0 0 0 46px;

      font-size: 16px;
      color: $text-color;
    }
  }
  &__delivery {
    @include button(40px, "https://nikitaushenko.github.io/react_uber-eats/images/Time.svg");
  }
  &__search {
    @include button(auto, "https://nikitaushenko.github.io/react_uber-eats/images/Loop.svg");
  }
  &__sign-in {
    text-decoration: none;
    color: $text-color;
    margin-left: 40px;
  }
}
