@import "utils";

.card {
  padding-bottom: 20px;
  cursor: pointer;

  &-image {
    width: 100%;
    height: 100%;
    margin-bottom: 10px;
    object-fit: cover;
    transition: 0.2s ease-in;

    &__wrapper {
      overflow: hidden;
      height: 280px;
      margin-bottom: 10px;
    }
  }

  &-title {
    margin: 0 0 5px;
    font-size: 16px;
    line-height: 19px;
    font-weight: normal;
  }

  &-breadcrumbs {
    color: $light-grey;
    display: block;
    width: 100%;

    white-space: nowrap;
    margin-bottom: 8px;

    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &-additional {
    display: flex;
  }

  &-time {
    padding: 0 10px;
    height: 24px;

    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 14px;

    background-color: $time-block;
  }

  &-rating {
    padding: 0 10px;
    height: 24px;
    font-size: 14px;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-left: 5px;

    background-color: $time-block;

    img {
      display: inline-block;
    }
  }

  &:hover img {
    transition: 0.2s ease-out;
    transform: scale(1.05);
  }
}
