@import "utils";

footer {
  background-color: #1f1f1f;
  color: #fff;
  padding: 72px 0;
}

.footer-navigation {
  display: flex;

  padding-bottom: 40px;
  border-bottom: 1px solid #fff;

  &__mobile-ver {
    display: none;
  }

  &__mobile {
    display: flex;
    flex: 2;
    flex-direction: column;
    justify-content: space-between;
  }

  &__mobile-logo {
    width: 150px;
  }

  &__mobile-link {
    margin-right: 16px;

    img {
      height: 40px;
    }
  }

  &__about {
    display: flex;
    flex-direction: column;
    flex: 1;
    a {
      color: #fff;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
      img {
        height: 16px;
        margin-right: 5px;
      }
    }
  }
  &__about-link {
    margin-bottom: 24px;
  }
}

.footer-links {
  display: flex;

  padding-top: 40px;
  &__copyright {
    font-size: 14px;
    opacity: 0.8;
    flex: 1;
  }

  &__policies {
    flex: 1;
  }

  &__policies-links {
    a {
      color: #fff;
      text-decoration: none;
      margin-right: 40px;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__policies-socials {
    margin-top: 60px;
    a {
      margin-right: 20px;

    }
    img {
      width: 16px;
    }
  }
}

@media (max-width: 640px) {
  footer {
    padding: 50px 0;
  }

  .footer-navigation {
    flex-direction: column;
    margin-bottom: 20px;
    padding-bottom: 20px;

    &__mobile-ver {
      display: flex;
      img {
        height: 35px;
      }
    }
    &__mobile div {
      display: none;
    }
    &__mobile-logo {
      margin-bottom: 40px;
    }
  }
  .footer-links {
    flex-direction: column;
    &__policies-socials {
      margin-top: 20px;
    }
    &__policies-links {
      display: flex;
      flex-direction: column;
      a {
        margin-bottom: 24px;
      }
    }
    &__copyright {
      margin-bottom: 24px;
    }
  }
}
