@import "utils";

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 1fr));
  grid-gap: 20px;
}

main {
  padding-bottom: 80px;
  padding-top: 30px;
}

@media (max-width: 1024px) {
  .grid-container {
    grid-template-columns: repeat(2, minmax(250px, 1fr));
  }
}

@media (max-width: 600px) {
  .grid-container {
    grid-template-columns: repeat(1, minmax(250px, 1fr));
  }
}
